// ** React Imports
import { Link, useLocation, useNavigate } from 'react-router-dom'
import themeConfig from '@src/configs/themeConfig'
// ** Icons Imports
import { ChevronLeft } from 'react-feather'

// ** Custom Components
import InputPasswordToggle from '@components/input-password-toggle'
import axios from '@src/configs/axios/axiosConfig'
import * as yup from 'yup'
import { useForm, Controller } from 'react-hook-form'
import { yupResolver } from '@hookform/resolvers/yup'
// ** Reactstrap Imports
import { Card, CardBody, CardTitle, CardText, Form, Label, Button, FormFeedback } from 'reactstrap'

// ** Styles
import '@styles/react/pages/page-authentication.scss'
import toast from 'react-hot-toast'

const ResetPasswordBasic = () => {

  const passwordRegx = /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*#?&])[A-Za-z\d@$!%*#?&]{8,}$/
  const search = useLocation().search
  const token = new URLSearchParams(search).get('token')
  const email = new URLSearchParams(search).get('email')
  const navigate = useNavigate()
  const SignupSchema = yup.object().shape({
    email: yup.string().email().default(email),
    token: yup.string().default(token),
    password: yup.string().required('Password is Required').matches(
      passwordRegx,
      "Password must Contain 8 Characters, One Uppercase, One Lowercase, One Number and one special case Character"),
    confirmPassword: yup
      .string()
      .required('Confirm Password is Required')
      .oneOf([yup.ref(`password`), null], 'Passwords must match')
  })

  // ** Hooks
  const {
    control,
    handleSubmit,
    formState: { errors }, setError
  } = useForm({
    resolver: yupResolver(SignupSchema)
  })

  const onSubmit = async data => {

    axios.post('/passwordresets/resetpassword', data)
      .then(() => {
        toast.success('Password Reset Successfully...', { position: 'bottom-right' })
        navigate('/login')
      }).catch((err) => { console.log(err); setError(err) })

  }

  return (
    <div className='auth-wrapper auth-basic px-2'>
      <div className='auth-inner my-2'>
        <Card className='mb-0'>
          <CardBody>
            <Link className='brand-logo' to='/' onClick={e => e.preventDefault()}>
              <img src={themeConfig.app.appLogoImage} alt={'Turia Logo'} width={45} height={35} />
              <h1 className='brand-text text-primary'>Turia</h1>
            </Link>
            <CardTitle tag='h4' className='mb-1'>
              Reset Password 
            </CardTitle>
            <CardText className='mb-2'>Your new password must be different from previously used passwords</CardText>
            <Form className='auth-reset-password-form mt-2' onSubmit={handleSubmit(onSubmit)}>
              <div className='mb-1'>
                <Label className='form-label' for='new-password'>
                  New Password
                </Label>
                <Controller
                  id='password'
                  name='password'
                  control={control}
                  render={({ field }) => (
                    <InputPasswordToggle
                      htmlFor='password'
                      className='input-group-merge'
                      invalid={errors.password && true}
                      {...field}
                    />
                  )}
                />
                {errors.password && <FormFeedback>{errors.password.message}</FormFeedback>}
              </div>
              <div className='mb-1'>
                <Label className='form-label' for='confirm-password'>
                  Confirm Password
                </Label>
                <Controller
                  control={control}
                  id='confirmPassword'
                  name='confirmPassword'
                  render={({ field }) => (
                    <InputPasswordToggle
                      htmlFor='confirmPassword'
                      className='input-group-merge'
                      invalid={errors.confirmPassword && true}
                      {...field}
                    />
                  )}
                />
                {errors.confirmPassword && <FormFeedback>{errors.confirmPassword.message}</FormFeedback>}
              </div>
              <Button color='primary' block>
                Set New Password
              </Button>
            </Form>
            <p className='text-center mt-2'>
              <Link to='/pages/login-basic'>
                <ChevronLeft className='rotate-rtl me-25' size={14} />
                <span className='align-middle'>Back to login</span>
              </Link>
            </p>
          </CardBody>
        </Card>
      </div>
    </div>
  )
}

export default ResetPasswordBasic
